import React from "react";
import PokeList from "./PokeList";
import pokemons from "../data/pokemons.json";

import "../stylesheets/App.scss";

class App extends React.Component {
  //trabajar con estado
  //hacer estado inical. No hace falta props porque de aquí es van a pasar a hija
  constructor() {
    super();
    //el estado es un obj que yo me invento. Aquí defino el estado inicial de ese obj
    this.state = {
      pokedex: pokemons,
    };
  }

  render() {
    // console.log(pokemons);
    return (
      <div>
        {/* traigo los datos del json */}
        <PokeList pokemons={pokemons} />;
      </div>
    );
  }
}

export default App;
